import React from 'react';
import { Row, Col } from 'antd';
import raritychart from '../assets/raritychart.png';
import gen2Rarity from '../assets/gen2Rarity.png';

export default function AboutPage() {
    return (
        <>
            <div style={{display: 'flex', justifyContent: 'center', paddingBottom:'2rem'}}>
              <img src={gen2Rarity} width="60%" height="60%"/>
            </div>
            <div style={{display: 'flex', justifyContent: 'center', paddingBottom:'2rem'}}>
              <img src={raritychart} width="60%" height="60%"/>
            </div>
        </>
    );
}